





































































































import { computed, defineComponent, PropType } from '@vue/composition-api';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';
import {
  FeatureListTileContent,
  FeatureListTileDesign, FeatureListTileLayout,
} from '@/app/shared/client/type/tile/feature-list-tile';
import TileTitle from '@/app/website/client/view/component/TileTitle.vue';

interface FeatureListTileProps {
  content: FeatureListTileContent;
  design: FeatureListTileDesign;
}

function computedLayoutClass(layout: FeatureListTileLayout | undefined): string {
  switch (layout) {
    case '1-COL':
      return '1-col';
    case '2-COL':
      return '2-col';
    case '3-COL':
      return '3-col';
    case '4-COL':
      return '4-col';
    default:
      return '1-col';
  }
}

const FeatureListTile = defineComponent({
  components: {
    TileTitle,
  },
  props: {
    content: {
      required: true,
      type: Object as PropType<FeatureListTileContent>,
    },
    design: {
      required: true,
      type: Object as PropType<FeatureListTileDesign>,
    },
    isHeadTile: {
      required: true,
      type: Boolean,
    },
  },
  setup(props: FeatureListTileProps) {
    const layoutClass = computed(() => `ins-tile--${computedLayoutClass(props.design.layout)}`);
    const hasTitle = computed(() => isNotNullOrEmpty(props.content.title));
    const hasSubtitle = computed(() => isNotNullOrEmpty(props.content.subtitle));
    const hasItems = computed(() => !!props.content.items.length);
    const hasItemIcons = computed(() => props.content.items.some((item) => item.iconUrl));

    return {
      layoutClass,
      hasTitle,
      hasSubtitle,
      hasItems,
      hasItemIcons,
    };
  },
});

export default FeatureListTile;
